import React from 'react';
import styled from 'styled-components';

const FooterS = styled.footer`
  background-color: lightgray;
  color: black;
  height: 420px;
  padding-top: 180px;
`;

const FooterWrapper = styled.div`
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  position: relative;
`;

const Ul = styled.ul`
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  list-style: none;
  font-size: 19px;
`;

const Social = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 180px;
  height: 50px;
  font-size: 29px;
  position: absolute;
  right: 0;
`;

const Footer = () => {
  return (
    <FooterS>
      <div className='container'>
        <img src='./images/logo.svg' alt='' />
        <FooterWrapper>
          <div className='huddle'>
            <br />
            <i
              style={{ margin: '10px 0', fontSize: '22px' }}
              class='fas fa-map-marker-alt'
            >
              <span style={{ marginRight: '17px' }}></span>
              Lorem ipsum dolor sit amet consectetur adipisicing elit.
            </i>
            <br />
            <i
              style={{ margin: '10px 0', fontSize: '22px' }}
              class='fas fa-phone-volume'
            >
              <span style={{ marginRight: '17px' }}></span>
              Lorem ipsum dolor sit amet.
            </i>
            <br />
            <i
              style={{ margin: '10px 0', fontSize: '22px' }}
              class='far fa-envelope'
            >
              <span style={{ marginRight: '17px' }}></span>
              Lorem, ipsum dolor.
            </i>
          </div>
          <Ul>
            <li style={{ height: '30%', cursor: 'pointer' }}>Home</li>
            <li style={{ height: '30%', cursor: 'pointer' }}>Home</li>
            <li style={{ height: '30%', cursor: 'pointer' }}>Home</li>
            <li style={{ height: '30%', cursor: 'pointer' }}>Home</li>
            <li style={{ height: '30%', cursor: 'pointer' }}>Home</li>
            <li style={{ height: '30%', cursor: 'pointer' }}>Home</li>
          </Ul>
          <Social>
            <i class='fab fa-facebook social-media'></i>
            <i class='fab fa-github social-media'></i>
            <i class='fab fa-linkedin social-media'></i>
          </Social>
        </FooterWrapper>
      </div>
    </FooterS>
  );
};

export default Footer;
